
'use client';

import React, { useState } from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { initiateAgentCall } from '@/ai/flows/initiate-agent-call-flow';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { useToast } from '@/hooks/use-toast';
import { LoaderCircle, PhoneOutgoing } from 'lucide-react';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { Textarea } from '@/components/ui/textarea';

const formSchema = z.object({
  incidentSummary: z.string().optional(),
});

export default function AgentCallTestPage() {
  const [isLoading, setIsLoading] = useState(false);
  const { toast } = useToast();

  const form = useForm<z.infer<typeof formSchema>>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      incidentSummary: 'This is a test call from the WitnessAI application.',
    },
  });

  async function onSubmit(values: z.infer<typeof formSchema>) {
    setIsLoading(true);
    try {
      const agentId = process.env.NEXT_PUBLIC_ELEVENLABS_AGENT_ID;
      const agentPhoneNumberId = process.env.NEXT_PUBLIC_ELEVENLABS_AGENT_PHONE_NUMBER_ID;
      const toNumber = process.env.NEXT_PUBLIC_EMERGENCY_CONTACT_NUMBER;

      if (!agentId || !agentPhoneNumberId || !toNumber) {
        throw new Error('Required environment variables for ElevenLabs are not set.');
      }

      const result = await initiateAgentCall({
        agentId,
        agentPhoneNumberId,
        toNumber,
        incidentSummary: values.incidentSummary,
      });

      if (result.success) {
        toast({
          title: 'Call Initiated Successfully',
          description: `Call SID: ${result.callSid || 'N/A'}. Conversation ID: ${result.conversation_id || 'N/A'}`,
        });
      } else {
        throw new Error(result.message || 'An unknown error occurred.');
      }
    } catch (e: any) {
      console.error(e);
      toast({
        variant: 'destructive',
        title: 'Failed to Initiate Call',
        description: e.message,
      });
    } finally {
      setIsLoading(false);
    }
  }

  return (
    <div className="bg-background min-h-screen">
      <main className="container mx-auto px-4 py-8 md:py-16">
        <header className="text-center mb-12">
          <h1 className="text-4xl sm:text-5xl font-bold font-headline text-primary">Agent Call Test</h1>
          <p className="mt-4 text-lg text-muted-foreground">Manually trigger an agentic call to ElevenLabs.</p>
        </header>
        <div className="w-full max-w-2xl mx-auto">
            <Card className="shadow-lg">
                <Form {...form}>
                <form onSubmit={form.handleSubmit(onSubmit)}>
                    <CardHeader>
                        <CardTitle>Call Parameters</CardTitle>
                        <CardDescription>
                            Agent details are loaded from environment variables. Provide the call context below.
                        </CardDescription>
                    </CardHeader>
                    <CardContent className="space-y-6">
                     <FormField
                        control={form.control}
                        name="incidentSummary"
                        render={({ field }) => (
                        <FormItem>
                            <FormLabel>Call Context</FormLabel>
                            <FormControl>
                            <Textarea
                                placeholder="Provide context for the agent..."
                                className="min-h-[100px]"
                                {...field}
                            />
                            </FormControl>
                            <FormMessage />
                        </FormItem>
                        )}
                    />
                    </CardContent>
                    <CardFooter>
                    <Button type="submit" disabled={isLoading} className="w-full text-lg py-6">
                        {isLoading ? (
                        <>
                            <LoaderCircle className="mr-2 h-5 w-5 animate-spin" />
                            Initiating Call...
                        </>
                        ) : (
                        <>
                            <PhoneOutgoing className="mr-2 h-5 w-5" />
                            Place Agent Call
                        </>
                        )}
                    </Button>
                    </CardFooter>
                </form>
                </Form>
            </Card>
        </div>
      </main>
    </div>
  );
}
